.App {
  text-align: center;
  background-color: #121212;
  width: 100vw;
  height: 100vh;
  /* display: flex; */
  color: white;
}

body {
  padding: 0%;
  margin: 0;
}

nav {
  height: 60px;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid white;
  display: grid;
  place-items: center;
}

nav h1 {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  color: white;
  font-size: 45px;
}
.game {
  width: 100vw;
  height: calc(100vh - 170px);
  display: flex;
  align-items: center;
  padding-top: 30px;
  flex-direction: column;
}

.board {
  width: 500px;
  height: 500px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
}

.row {
  flex: 5%;
  display: flex;
  flex-direction: row;
  margin: 0px;
}

.letter {
  width: 60px;
  height: 50px;
  border: 1px solid grey;
  margin: 3px;
  display: grid;
  place-items: center;
  font-size: 30px;
  font-weight: bolder;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
          user-select: none; /* Non-prefixed version, currently
                                supported by Chrome, Edge, Opera and Firefox */
}

#correct {
  background-color: #b49f39;
}

#fix {
  background-color: #528d4e;
}

#highlight {
  width: 58px;
  height: 47px;
  border: 2px solid white;
}

#select {
  width: 52px;
  height: 42px;
  border: 5px solid white;
}

#fixhighlight {
  width: 58px;
  height: 47px;
  border: 2px solid white;
  background-color: #528d4e;
}

#fixselect {
  width: 52px;
  height: 42px;
  border: 5px solid white;
  background-color: #528d4e;
}

#correcthighlight {
  width: 58px;
  height: 47px;
  background-color: #b49f39;
  border: 3px solid white;
}

#correctselect {
  width: 52px;
  height: 42px;
  background-color: #b49f39;
  border: 5px solid white;
}


.keyboard {
  width: 700px;
  height: 300px;
  margin-top: 60px;
}

.line1 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin: 5px;
}
.line2 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}
.line3 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}

.key {
  width: 50px;
  height: 70px;
  margin: 5px;
  border-radius: 4px;
  display: grid;
  place-items: center;
  font-size: 20px;
  background-color: grey;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
}

#big {
  width: 100px;
}